_vm._v(" 페이버리스 전자 계약 서비스 런칭 프로젝트 때의 일입니다. 마케팅/메인 웹 화면 퍼블리싱을 전담하면서도, 프론트엔트 개발자로서의 역량을 키우기 위해 직접 자원하여 화면 개발에 참여했습니다. 데이터를 수신, 비교하고 송신하는 과정이 예상했던 것보다 복잡했지만, 문제 해결을 위해 퇴근 후 ‘Vue’ ‘Javascript’ 강의를 꾸준히 수강했고, 코딩테스트 가능한 플랫폼을 활용하여, 개발자 역량을 강화해왔습니다. 그 결과 '내 정보 수정'화면 개발을 100% 전담하여 무사히 마칠 수 있었습니다. "),_c('br'),_c('br'),_vm._v(" 어떤 프로젝트를 수행하더라도 개발하기 편한 코드가 아닌 항상 사용자 친화적인 프로덕트를 구현하기 위한 코드를 고민하는 것이 중요하다고 생각합니다. 끊임없는 배움과 도전을 통해 원클릭으로 고객이 원하는 정확한 정보를 제공하는 직관적인 화면을 구현하는 개발자가 되고 싶습니다. ")]),_c('h3',[_vm._v("핵심 역량 - 시각화로 정확하고 명료한 '커뮤니케이션'")]),_c('div',{staticClass:"cv-text"},[_vm._v(" 프로젝트를 성공적으로 수행하기 위해서는 다양한 분야의 팀 간의 정확하고 명료한 소통이 필수입니다. 특히, 각 팀의 상황을 시각적인 자료를 활용하여 보다 쉽게 이해하고 소통할 경우, 문제를 더 신속하고 정확하게 처리할 수 있습니다. "),_c('br'),_c('br'),_vm._v(" '교원 차세대 영업 관리 시스템 구축 프로젝트' 때 일입니다. 인도 GDC와의 협업해 대규모 페이지를 제작해야하는 상황에서 언어적 한계뿐만 아니라 고객 니즈에 따른 지속적인 공통 컴포넌트 레이아웃 변화로 페이지 완성도와 기한에 문제가 생겼습니다. 이를 해결하기 위해 팀원들과 협업하여, 공통 레이아웃과 컴포넌트 등 직접 웹 퍼블리싱하고 적재적소에 배치하도록 작업 가이드를 메뉴얼화해 인도 개발자에게 제공했습니다. 협업 툴을 사용해 소통 단계를 단순화해 실시간 업데이트와 검수를 가능하게 했습니다. "),_c('br'),_vm._v(" 내부 기획자들에게 웹 기획서를 기반으로 인도 개발자가 페이지 제작 시의 언어적 한계 문제를 설명하여, 공통 컴포넌트를 직관적으로 알수있게 시각적으로 통일해 제공하도록 요청하였습니다. 이러한 노력으로 3주 지연 가능성 문제를 극복하고 기간 내에 마무리했으며, 고객사로부터는 기존 GDC가 참여한 프로젝트 중 가장 완성도 높은 웹 퍼블리싱 화면으로 평가받았습니다. "),_c('br'),_c('br'),_vm._v(" 문제 발생 시 원인을 논리적으로 파악하고, 시각화된 자료를 톻해 원인과 해결점을 직관적으로 소통해 효과적으로 해결하는 능력을 갖고 있습니다. 앞으로도 예상치 못한 문제에 적극적으로 의사소통하고 협업하여 더 나은 결과물을 위해 지속적으로 발전하는 개발자로 성장하고자 합니다. ")])])])])])
